body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 15px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.title {
  text-align: center;
  color: #333;
}

.breed-select {
  cursor: pointer;
  width: 20%;
  color: #333;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 20px;
  margin-bottom: 40px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  width: 164px;
  height: 164px;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 40px;
  height: 40px;
  background-color: #fff;
  animation: rotate 1s ease-in infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:after {
  width: 20px;
  height: 20px;
  background-color: #ff3d00;
  animation: rotate 1s ease-in infinite, moveY 1s ease-in infinite;
}

@keyframes moveY {
  0%, 100% {
    top: 10%;
  }

  45%, 55% {
    top: 59%;
  }

  60% {
    top: 40%;
  }
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -100%)rotate(0deg)scale(1);
  }

  25% {
    transform: translate(-50%)rotate(180deg)scale(1);
  }

  45%, 55% {
    transform: translate(-50%, 100%)rotate(180deg)scale(3, .5);
  }

  60% {
    transform: translate(-50%, 100%)rotate(180deg)scale(1);
  }

  75% {
    transform: translate(-50%)rotate(270deg)scale(1);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg)scale(1);
  }
}

.cat-info {
  width: 800px;
  text-align: center;
  margin: 10px auto 0;
}

.image {
  max-width: 800px;
  border: 3px solid #010101;
  margin-bottom: 5px;
}

.name {
  color: #333;
  margin-bottom: 5px;
  font-size: 24px;
}

.description {
  color: #555;
  margin-bottom: 5px;
}

.temperament {
  color: #333;
}

